"use client"

import { useState } from "react"
import { Card } from "@/components/ui/card"
import { Button } from "@/components/ui/button"

interface MenuItem {
  id: number
  name: string
  price: number
}

const menuItems: MenuItem[] = [
  {
    id: 1,
    name: "Filtre Kahve",
    price: 25
  },
  {
    id: 2, 
    name: "Sıcak Çikolata",
    price: 30
  },
  {
    id: 3,
    name: "Shıtı Latte",
    price: 30
  },
  {
    id: 4,
    name: "Türk Kahvesi",
    price: 25
  }
]

export default function Home() {
  const [quantities, setQuantities] = useState<{[key: number]: number}>({})

  const updateQuantity = (id: number, delta: number) => {
    setQuantities(prev => ({
      ...prev,
      [id]: Math.max(0, (prev[id] || 0) + delta)
    }))
  }

  const handleBuy = (id: number) => {
    if (quantities[id] > 0) {
      // Here you would integrate with FiveM
      console.log(`Buying ${quantities[id]} of item ${id}`)
    }
  }

  return (
    <main className="min-h-screen bg-black/80 p-8">
      <div className="max-w-5xl mx-auto">
        <div className="bg-[#006241] p-8 rounded-2xl shadow-2xl relative">
          <div className="flex items-center justify-between mb-8">
            <h1 className="text-4xl font-bold text-white tracking-wide">Starbucks</h1>
            <button className="text-white hover:text-gray-200">
              <svg 
                viewBox="0 0 24 24" 
                className="w-6 h-6"
                fill="none"
                stroke="currentColor"
                strokeWidth="2"
              >
                <path d="M6 18L18 6M6 6l12 12" />
              </svg>
            </button>
          </div>
          
          <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-4 gap-6">
            {menuItems.map((item) => (
              <Card key={item.id} className="bg-[#1e3932] text-white p-6 rounded-2xl border-0 shadow-lg hover:shadow-xl transition-all duration-200 hover:scale-[1.02] relative">
                <div className="flex flex-col items-center">
                  <div className="w-32 h-32 mb-6 flex items-center justify-center bg-[#006241] rounded-full p-6 shadow-lg">
                    <svg 
                      viewBox="0 0 24 24" 
                      className="w-full h-full text-white"
                      fill="none"
                      stroke="currentColor"
                      strokeWidth="1.5"
                    >
                      <path d="M18 8h1a4 4 0 0 1 0 8h-1M2 8h16v9a4 4 0 0 1-4 4H6a4 4 0 0 1-4-4V8zm4-7v3M12 1v3M8 1v3" />
                    </svg>
                  </div>
                  
                  <h3 className="text-xl font-semibold mb-3">{item.name}</h3>
                  <p className="text-2xl font-bold mb-8">{item.price}₺</p>
                  
                  <div className="flex items-center gap-6 mb-8 w-full justify-center">
                    <Button
                      variant="outline"
                      size="icon"
                      onClick={() => updateQuantity(item.id, -1)}
                      className="w-8 h-8 rounded-full bg-red-600 hover:bg-red-700 text-white border-0 shadow-md flex items-center justify-center text-lg font-bold"
                    >
                      -
                    </Button>
                    <span className="w-8 text-center text-lg font-medium">
                      {quantities[item.id] || 0}
                    </span>
                    <Button
                      variant="outline"
                      size="icon"
                      onClick={() => updateQuantity(item.id, 1)}
                      className="w-8 h-8 rounded-full bg-green-600 hover:bg-green-700 text-white border-0 shadow-md flex items-center justify-center text-lg font-bold"
                    >
                      +
                    </Button>
                  </div>

                  <Button
                    onClick={() => handleBuy(item.id)}
                    className="w-full py-2.5 bg-green-600 hover:bg-green-700 text-white rounded-full font-medium text-base transition-all duration-200 shadow-md"
                    disabled={!quantities[item.id]}
                  >
                    Satın Al
                  </Button>
                </div>
              </Card>
            ))}
          </div>
        </div>
      </div>
    </main>
  )
}
